import React, { useContext } from 'react'
import styles from './Navbar.module.css';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/freshcart-logo.svg'
import { cartContext } from '../../context/CartContext';

export default function Navbar({ userData, logout }) {


  let { numOfCartItems } = useContext(cartContext)

  return <>

    <nav className="navbar navbar-expand-sm fixed-top navbar-light bg-light">
      <div className="container">
        <Link className="navbar-brand" to="#"><img src={logo} /></Link>
        <button className="navbar-toggler d-lg-none" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId"
          aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavId"><>


          {userData !== null ?

            <>  <ul className="navbar-nav me-auto mt-2 mt-lg-0">

              <li className="nav-item">
                <Link className="nav-link" to="/">Home</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="cateogry">Cateogry</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="products">Products</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="brands">Brand</Link>
              </li>

            </ul>

              <ul className="navbar-nav me-0 mt-2 mt-lg-0">

                <li className='d-flex justify-content-center align-items-center'>
                  <i className='fab px-1 fa-facebook'></i>
                  <i className='fab px-1 fa-twitter'></i>
                  <i className='fab px-1 fa-linkedin'></i>
                  <i className='fab px-1 fa-instagram'></i>
                  <i className='fab px-1 fa-youtube'></i>

                </li>

                <li className="nav-item position-relative">
                  <Link className="nav-link mx-3" to="cart">
                    <i className='fas fa-shopping-cart'></i>
                    <span className='bg-main position-absolute top-0 end-0 badge text-white'>{numOfCartItems}</span>  </Link>
                </li>

                <div className="btn-group">
                  <button type="button" className="btn btn-outline-gray mx-1 dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                  </button>
                  <ul className="dropdown-menu dropdown-menu-end">

                    <li><Link className="dropdown-item" to={'/passwordLoggedChange'}>Change Password</Link></li>
                    <li className="nav-item dropdown-item "> <span onClick={logout} className="nav-link">Logout</span></li>
                  </ul>
                </div>



              </ul>

            </> : <ul className="navbar-nav me-0 mt-2 mt-lg-0">

              <li className="nav-item">
                <Link className="nav-link" to="login">Login</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="register">Register</Link>
              </li>


            </ul>}

        </>
        
        </div>
      </div>
    </nav>

  </>
}
