import React, { useEffect, useState } from 'react'
import styles from './ResetPassword.module.css';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { Helmet } from 'react-helmet';




export default function ResetPassword() {

  let navigate = useNavigate()
  const [loading, setloading] = useState(false)
  const [mesggeerror, setmesggeerror] = useState('')

async function sendedCode(values)
{
  setloading(true)

  let data  = await axios.post(`https://route-ecommerce-app.vercel.app/api/v1/auth/verifyResetCode` , values ).catch((err)=>{
  setloading(false)
  setmesggeerror(`${err.response.data.message}`)
  console.log(data);
})
  if(data.data.status === 'Success')
  {
    // localStorage.setItem('userToken' , data.token)
    setloading(false)
    // saveUserData();
      
    navigate('/changePassword')
  }
  console.log(data);
}

let validation = Yup.object({
  resetCode:Yup.string().required('code is required').min(4,'minmum 6 numbers' ).max(6, 'numbers'),
})

let formik = useFormik({
  initialValues:{
    resetCode:'',
  },

  validationSchema:validation,
  onSubmit:sendedCode

});



  return <>
       <Helmet>
                <title>Reset Passcode</title>
            </Helmet>


   <div className="container">

   <div className="w-75 py-4 mx-auto">
   <form onSubmit={formik.handleSubmit}>

   <h2>Reset Passcode</h2>

   {mesggeerror ? <div className="alert alert-danger">{mesggeerror}</div> : null } 


   <label htmlFor="resetCode">reset Code:</label>
   <input onBlur={formik.handleBlur} onChange={formik.handleChange}  value={formik.values.resetCode} className='form-control my-2 w-100' type="text" name='resetCode' id='resetCode' />
   {formik.errors.resetCode && formik.touched.resetCode ? <div className="alert alert-success">{formik.errors.resetCode}</div>: null } 

   {loading ? <button  className='btn bg-main text-white' type='submit'><i className='fas fa-spinner fa-spin'></i></button>

   : <button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white' type='submit'>verify</button> }
  
   </form>
   </div>

   </div>
  </>
}
