import React, { useEffect, useState } from 'react'
import styles from './BrandDetails.module.css';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
export default function BrandDetails() {

  const [brandDetails, setbrandDetails] = useState({})

  let { id } = useParams()

   //******************************************************************** 
   // specific brand details data 
   //********************************************************************

  async function getBrandDetails() {
    let { data } = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/brands/${id}`)
    setbrandDetails(data.data)
    console.log(data.data)
  }

  useEffect(() => {
    getBrandDetails()
  }, [])

  return <>
     <Helmet>
                <title>Brand Details</title>
            </Helmet>
    <h2 className='text-main m-5 text-center'>Brand Details</h2>
    <div className="container m-5">
      <div className="row ">

        <div className="col-md-3">
          <img src={brandDetails.image} alt="" />
        </div>

        <div className="col-md-9">
          <h1 className=' text-main'>{brandDetails.name}</h1>
          <p className='Text-muted'>The company, first named PCs Limited, was founded in 1984 by American Michael Dell, who was then a student at the University of Texas in Austin. Initially running the business from a dormitory room, Dell started out providing customized upgrades for PCs. The venture proved profitable, and Dell dropped out of college that same year to begin building PCs. In 1985 the company released the Turbo PC, the first computer featuring Dells own design. Founded on the premise of creating and selling custom-built PCs directly to consumers, the company initially sold its products through advertisements and mail-order catalogs. By avoiding the costs associated with traditional retail markets, Dell was able to offer high-quality PCs at competitive prices. Dell emphasized customer support, sending technicians to service PCs and implementing a policy of risk-free returns. This business model proved successful, and the company quickly grew, expanding into international markets. The company, renamed Dell Computer Corporation, went public in 1988.</p>

        </div>
      </div>
    </div>


  </>
}
