import React, { useContext, useEffect, useState } from 'react'
import styles from './Products.module.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { cartContext } from '../../context/CartContext';
import { toast } from 'react-hot-toast';
import { Helmet } from 'react-helmet';


export default function Products() {
let {addToCart ,setnumOfCartItems} = useContext(cartContext)

const [products, Setproducts] = useState([])

 async function addProductToCart(productId)
{
 
  let response = await addToCart(productId)
  if(response.data.status === "success")
  {
    setnumOfCartItems(response.data.numOfCartItems)
    toast.success(response.data.message ,{duration:2000})
  }
  console.log(response);

}

 async function getProducts()
  {
    let {data} = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/products`)
    Setproducts(data.data)
    console.log(data.data)

  }
  useEffect(()=>{
    getProducts()
  },[])

  return <>
       <Helmet>
                <title>Products</title>
            </Helmet>
  <div className="container">
    <div className="row">
      {products.map((product)=>
      <div key={product._id} className="col-md-3">
        <div className="product cursor-pointer p-2">
      
         <Link to={/productDetiles/+product.id}><img className='w-100 ' src={product.imageCover} alt="" /></Link>
        <h2 className='h6 p-1'> {product.title.split(' ').slice(0,2).join(' ')}</h2>
        <div className="details d-flex justify-content-between">
          <span className='p-1'>{product.price} EGP.</span>
          <span className='p-1'><i className='fas fa-star rating-color'></i>{product.ratingsAverage}</span> 
        </div>
 
        <div className='p-2'>
        <button onClick={()=>addProductToCart(product._id)} className='btn bg-main w-100 m-auto'>Add To Cart</button>
        </div> 

        </div>

    </div>

      )}
      
    </div>
  </div>
  
  </>
}

