import React, { useEffect, useState } from 'react'
import styles from './Cateogry.module.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/grocery-banner-2.jpeg'
import { Helmet } from 'react-helmet';

export default function Cateogry() {




  const [cateogry, Setcateogry] = useState([])

  
 // get all categories 
  async function getCateogry()
   {
     let {data} = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/subcategories`)
     Setcateogry(data.data)
     console.log(data.data)
 
   }
   useEffect(()=>{
    getCateogry()
   },[])
 
  
  return <>
       <Helmet>
                <title>All Categories</title>
            </Helmet>
   <h2 className='text-main text-center m-5'>cateogry</h2>

    <div className="row">
      {cateogry.map((categry)=>

<div key={categry._id} className="col-md-3 product">
<img className='w-100 p-2' src={logo} alt="" />
<Link to={/categoryDetails/+categry._id}>
<h1 className='h6 m-2 text-center text-main'>{categry.name}</h1>
</Link>
</div>
      )}
   
    </div>




  
  </>
}
