import React, { useEffect, useState } from 'react'
import styles from './CategorySlider.module.css';
import axios from 'axios';
import Slider from 'react-slick';

// home categoury slider

export default function CategorySlider() {

  const [Categories, setCategories] = useState([])
  async function getCategorirs() {
    let { data } = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/categories`)
    setCategories(data.data)

  }
  useEffect(() => {
    getCategorirs()

  }, []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 9,
    slidesToScroll: 1
  };

  return <>

    {/* <h2>CategorySlider</h2> */}

    <div className="row g-0 m-5">

      <Slider {...settings}>

        {Categories.map((categ) =>
          <div key={categ._id} className="m-3">
            <div >

              <img src={categ.image} height={200} className='w-100' alt="" />
              <h2 className='h6'>{categ.name}</h2>

            </div>
          </div>

        )}
      </Slider>

    </div>

  </>
}
