import React, { useState } from 'react'
import styles from './ForgetPassword.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Helmet } from 'react-helmet';


export default function ForgetPassword() {
  let navigate = useNavigate()
  const [loading, setloading] = useState(false)
  const [mesggeerror, setmesggeerror] = useState('')

  async function getNewpasswordCode(values) {
    setloading(true)

    let data = await axios.post(`https://route-ecommerce-app.vercel.app/api/v1/auth/forgotPasswords`, values).catch((err) => {
      setloading(false)
      setmesggeerror(`${err.response.data.message}`)
      console.log(data);
    })
    if (data.data.statusMsg === 'success') {
      setloading(false)
      navigate('/resetPassword')
    }
    console.log(data);
  }

  let validation = Yup.object({
    email: Yup.string().required('E-mail is required').email('mail is in valied'),
  })

  let formik = useFormik({
    initialValues: {
      email: '',
    },

    validationSchema: validation,
    onSubmit: getNewpasswordCode

  });

  return <>
       <Helmet>
                <title>Forget Password</title>
            </Helmet>

    <div className="container">

      <div className="w-75 py-4 mx-auto">
        <form onSubmit={formik.handleSubmit}>

          <h2>Forget Password:</h2>

          {mesggeerror ? <div className="alert alert-danger">{mesggeerror}</div> : null}

          <label htmlFor="email">Email:</label>
          <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} className='form-control my-2 w-100' type="email" name='email' id='email' />
          {formik.errors.email && formik.touched.email ? <div className="alert alert-success">{formik.errors.email}</div> : null}

          {loading ? <button className='btn bg-main text-white' type='submit'><i className='fas fa-spinner fa-spin'></i></button>

            : <button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white' type='submit'>Send</button>}

        </form>
      </div>

    </div>
  </>

}
