import React, { useEffect, useState } from 'react'
import styles from './ChangePassword.module.css';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useFormik } from 'formik';
import * as Yup from 'yup'




export default function ChangePassword() {

  let navigate = useNavigate()

  const [loading, setloading] = useState(false)
  const [mesggeerror, setmesggeerror] = useState('')


  // data needed to change password 
  async function handelNewPassword(values) {
    setloading(true)

    let { data } = await axios.put(`https://route-ecommerce-app.vercel.app/api/v1/auth/resetPassword`, values).catch((err) => {
      setloading(false)
      setmesggeerror(`${err.response.data.message}`)
    })
    if (data.token) {
      setloading(false)
      navigate('/login')


    }

    console.log(data);
  }



  let validation = Yup.object({
    email: Yup.string().required('E-mail is required').email('mail is in valied'),
    newPassword: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with upper case '),
  })

  let formik = useFormik({
    initialValues: {
      email: '',
      newPassword: '',
    },


    validationSchema: validation,
    onSubmit: handelNewPassword
  });
  return <>

    <div className="container">
      <div className="w-75 py-4 mx-auto">
        <form onSubmit={formik.handleSubmit}>

          <h2>ChangePassword Now :</h2>

          {mesggeerror ? <div className="alert alert-danger">{mesggeerror}</div> : null}

          <label htmlFor="email">Email:</label>
          <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} className='form-control my-2 w-100' type="email" name='email' id='email' />
          {formik.errors.email && formik.touched.email ? <div className="alert alert-success">{formik.errors.email}</div> : null}

          <label htmlFor="newPassword">new Password:</label>
          <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.newPassword} className='form-control my-2 w-100' type="password" name='newPassword' id='newPassword' />
          {formik.errors.newPassword && formik.touched.newPassword ? <div className="alert alert-success">{formik.errors.newPassword}</div> : null}

          {loading ? <button className='btn bg-main text-white' type='submit'><i className='fas fa-spinner fa-spin'></i></button>

            : <button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white' type='submit'>Regist Now</button>}

        </form>
      </div>

    </div>
  </>
}
