import React, { useEffect, useState } from 'react'
import styles from './ProductDetiles.module.css';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import Slider from "react-slick";
import CategorySlider from '../CategorySlider/CategorySlider';
import { Helmet } from 'react-helmet';


export default function ProductDetiles() {

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  
let {id} = useParams()
// console.log(id);
const [prouctDetails, setprouctDetails] = useState({})
async function getProductDetiels()
{
 let{data} = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/products/${id}`)
  console.log(data.data)
  setprouctDetails(data.data)
  
}
useEffect(() => {

  getProductDetiels()

}, [])







  return <>
       <Helmet>
                <title>Product Details</title>
            </Helmet>

{/* <MainSlider/> */}

  {/* <h2 className='text-main text-center my-2'>ProductDetiles</h2> */}

  <div className="container">
    <div className="row d-flex justify-content-between">
      <div  className="col-md-4 m-auto">
       
        <Slider {...settings}>

          {prouctDetails?.images?.map((img)=> <img key={prouctDetails.id} src={img} alt="" /> )}
    
         </Slider>
        
  
      </div>



      <div className="col-md-8 m-auto">
        <h2 className=''>{prouctDetails.title}</h2>
        <h3 className='h5 text-muted'>{prouctDetails.description}</h3>
        <div className='d-flex justify-content-between'>
        <span className='text-muted' >{prouctDetails.price} EGP</span>

          <span ><i className='fas fa-star rating-color'></i> {prouctDetails.ratingsAverage}</span>
        </div>
        <button className='w-100 btn bg-main my-4'>Add To Cart</button>



      </div>
    </div>
  </div>
  <CategorySlider />
  </>
}
