import React, { useEffect, useState } from 'react'
import styles from './Brands.module.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";
export default function Brands() {

  const [brands, Setbrands] = useState([])

   //******************************************************************** 
   //  brands 
   //********************************************************************

  async function getBrands()
   {
     let {data} = await axios.get(`https://route-ecommerce-app.vercel.app/api/v1/brands`)
     Setbrands(data.data)
     console.log(data.data)
 
   }
   useEffect(()=>{
    getBrands()
   },[])
 
  return <>
              <Helmet>
                <title>All Brands</title>
              </Helmet>

   <h2 className='text-main text-center my-3'>Brands</h2>
    <div className="container mb-5">
      <div className="row">
        {brands.map((brand)=>

               <div key={brand._id} className="col-md-3 product">
                <Link to={/brandDetails/+brand._id}><img className='w-100 cursor-pointer' src={brand.image} alt="" /></Link> 
                 <h1 className='h6 text-main text-center'>{brand.name}</h1>
               </div>
      )}
   
    </div>
  </div>




  
  </>
}
