import React, { useContext, useEffect, useState } from 'react'
import styles from './Cart.module.css';
import { cartContext } from '../../context/CartContext';
import { toast } from 'react-hot-toast';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

export default function Cart() {

  let { getLoggedUserCart, removeItemFromCart, upDateCountProduct, setnumOfCartItems, ClearCartData } = useContext(cartContext)

  const [Cartdetails, setCartdetails] = useState(null)


  //******************************************************************** 
  // get all products in the cart when user login 
  //********************************************************************



  useEffect(() => {

    getProductsCart()

  }, [])
  //******************************************************************** 
  // prouducts off the user in the cart
  //********************************************************************

  async function getProductsCart() {
    let response = await getLoggedUserCart()
    if (response?.data?.status === "success") {

      setCartdetails(response.data.data)
      // console.log(response.data.data);
    }


  }

  //******************************************************************** 
  // deleting item of the user cart 
  //********************************************************************


  async function deleteItem(productId) {

    let response = await removeItemFromCart(productId)

    setCartdetails(response.data.data)
    if (response.data.status === "success") {
      setnumOfCartItems(response.data.numOfCartItems)
      toast.success('your Product have been deleted', { duration: 2000 })
    }
    // console.log(response);

  }
  
  //******************************************************************** 
  // add or less quantity of item in the cart
  //********************************************************************



  async function upDateCount(productId, count) {
    let response = await upDateCountProduct(productId, count)
    setCartdetails(response.data.data)
    if (response.data.status === "success") {
      toast.success('your Product have been updated', { duration: 2000 })
    }

    // console.log(response);

  }




  async function clearcart() {
    let response = await ClearCartData()
     
    if (response.data.message === "success") {
      setCartdetails(response.data.data)
      toast.success('your cart is empty', { duration: 2000 })
    }

    console.log(response);

  }
  
  return <>
              <Helmet>
                <title>Cart</title>
            </Helmet>
    <div className="mt-5 mb-5">
      <h2 className='text-main text-center'>Cart Details</h2>
    </div>
    {Cartdetails !== null ?

      <div className="bg-main-light p-4 my-4">
        <h4 className='text-muted'>your products</h4>
        <h6 className='text-main'>Total Amount : {Cartdetails?.totalCartPrice}</h6>

        <div className='m-5 '></div>

        {Cartdetails?.products?.map((product) =>

          <div key={product._id} className="row border-bottom py-2 align-items-center mx-2">

            <div className="col-md-1">
              <img className='w-100 p-2' src={product.product.imageCover} alt="" />

            </div>
            <div className="col-md-11 d-flex justify-content-between">
              <div>
                <h6 >{product.product.brand.name}</h6>
                <h6>{product.product.title}</h6>
                <h6 className='text-main'> price: {product.price} EGP</h6>
                <button onClick={() => deleteItem(product.product.id)} className='btn m=0 p-0'> <i className='fa-regular text-main fa-trash-can'></i> Remove</button>

              </div>

              <div>
                <button onClick={() => upDateCount(product.product.id, product.count + 1)} className='btn btn-sm btn-main'>+</button>
                <span className='m-1' >{product.count}</span>
                <button onClick={() => upDateCount(product.product.id, product.count - 1)} className='btn btn-sm btn-main'>-</button>
              </div>
            </div>

          </div>


        )}
 <div className="container">
              <button onClick={() => clearcart()} className='btn btn-main my-4 mx-1'> <i className='fa-regular text-main fa-trash-can'></i> Remove all cart</button>

              <button className='btn bg-main my-4 align-items-center'>
                <Link to={'/checkout'}>
                  pay online</Link>
              </button>
            </div>

      </div>

      :

      <div className='container  '>
        <div className="row m-5">
          <div className="col-md-12 d-flex justify-content-center align-items-center">
            <h6 className='h1'>you cart is empty</h6>
            <i  className='text-main fs-cart fas fa-shopping-cart'></i> 
          </div>
        </div>
      </div>
    }

  </>
}
