import axios from "axios";
import { createContext, useEffect, useState } from "react";




 export let cartContext = createContext();


 export function CartContextProvider(props)
{
    const [cartId, setcartId] = useState(null)
    const [numOfCartItems, setnumOfCartItems] = useState(0)


    async function getCart()
    {
        
     let response = await getLoggedUserCart()

     if(response?.data?.status === 'success')
     {

        setcartId(response.data.data._id)
        setnumOfCartItems(response.data.numOfCartItems)

     }

     console.log(response);
    }

    useEffect(() => {
        getCart()

    }, [])
    
    let headers = {token:localStorage.getItem('userToken')} 

    function addToCart(productId)
    {
       return axios.post(`https://route-ecommerce-app.vercel.app/api/v1/cart`,
        {
            productId:productId

        },
        {
            headers:headers


        }).then((response)=>response)
        .catch((error)=> error)
    }

    function getLoggedUserCart()
    {
       return axios.get(`https://route-ecommerce-app.vercel.app/api/v1/cart`,
        {
            headers:headers


        }).then((response)=>response)
        .catch((error)=> error)
    }

    function removeItemFromCart(productId)
    {
       return axios.delete(`https://route-ecommerce-app.vercel.app/api/v1/cart/${productId}`,
        {
            headers:headers


        }).then((response)=>response)
        .catch((error)=> error)
    }

    function upDateCountProduct(productId , count)
    {
        return axios.put(`https://route-ecommerce-app.vercel.app/api/v1/cart/${productId}`,
        {
            count:count

        },
        {
            headers:headers

        }).then((response)=>response)
        .catch((error)=> error)
        
    }

    function onLinePayment(cartId , shippingAddress)
    {
        return axios.post(`https://route-ecommerce-app.vercel.app/api/v1/orders/checkout-session/${cartId}?url=http://localhost:420`,
        {
            shippingAddress:shippingAddress

        },
        {
            headers:headers

        }).then((response)=>response)
        .catch((error)=> error)
        
    }

    function ClearCartData()
    {
        return axios.delete(`https://route-ecommerce-app.vercel.app/api/v1/cart`,
        {
            headers:headers

        }).then((response)=>response)
        .catch((error)=> error)
        
    }

   
  return<>

    <cartContext.Provider value={{setnumOfCartItems , cartId, numOfCartItems, onLinePayment ,addToCart , getLoggedUserCart ,removeItemFromCart ,ClearCartData,upDateCountProduct }}>
    
    {props.children}
    </cartContext.Provider>


 </>


}
