import React, { useEffect, useState } from 'react'
import styles from './CategoryDetails.module.css';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import logo from '../../assets/images/grocery-banner-2.jpeg'
import logo1 from '../../assets/images/grocery-banner.png'
import logo2 from '../../assets/images/light-patten.svg'
import logo3 from '../../assets/images/blog-img-1.jpeg'
import logo4 from '../../assets/images/slider-2.jpeg'
import CategorySlider from '../CategorySlider/CategorySlider';
import { Helmet } from 'react-helmet';

export default function CategoryDetails() {
  let { id } = useParams()
  const [ctegDetails, setctegDetails] = useState({})

  //  get specific Category 
  async function getCategoryDetails() {
    let { data } = await axios(`https://route-ecommerce-app.vercel.app/api/v1/subcategories/${id}`)
    console.log(data.data);
    setctegDetails(data.data)
  }
  useEffect(() => {
    getCategoryDetails()
  }, [])


  return <>
              <Helmet>
                <title>Category Details</title>
            </Helmet>
<div className="row">
<h2 className='text-center text-main mt-3 mb-3'>Category Details</h2>
</div>
    

      <div className="row">
        <div className="col-md-4">
          <img className='w-100 p-2' src={logo} alt="" />

        </div>
        <div className="col-md-8">
          <h2 className='text-main p-2'>{ctegDetails.name}</h2>
          <p className='text-muted'>we have all you need in one place discover our web site for all categouries and enjoy you shopping 24/7 we have fashion , muli-media , home accesories ,Home appliance, and all you need </p>
        </div>
      </div>
  
    <CategorySlider />


  </>
}
