import React, { useContext } from 'react'
import styles from './CheckOut.module.css';
import { useFormik } from 'formik';
import { cartContext } from '../../context/CartContext';
import { Helmet } from 'react-helmet';



export default function CheckOut() {


  let { onLinePayment, cartId } = useContext(cartContext)

  // needed data to checkout user order and pay by strip. 
  async function handleSubmit(values) {

    let response = await onLinePayment(cartId, values)
    if (response.data.status === 'success')
      window.location.href = response.data.session.url
    console.log(response);

  }

  let formik = useFormik({

    initialValues: {
      details: '',
      city: '',
      phone: ''
    },
    onSubmit: handleSubmit

  })

  return <>
       <Helmet>
                <title>CHeck  out</title>
            </Helmet>

    <div className="w-50 py-5 mx-auto">


      <h2 className='h6 text-main' > take care filling your information details </h2>
      <form onSubmit={formik.handleSubmit}>

        <label htmlFor="details">Details</label>
        <input type="text" name='details' id='details' className='form-control' value={formik.values.details} onChange={formik.handleChange} />

        <label htmlFor="city">City</label>
        <input type="text" name='city' id='city' className='form-control' value={formik.values.city} onChange={formik.handleChange} />

        <label htmlFor="phone">Phone</label>
        <input type="tel" name='phone' id='phone' className='form-control' value={formik.values.phone} onChange={formik.handleChange} />

        <button type='submit' className='w-100 my-2 btn btn-main'> pay</button>








      </form>
    </div>

  </>
}
