import React, { useState } from 'react'
import styles from './PasswordLoggedChange.module.css';
import {  useFormik } from 'formik';
import * as Yup from 'yup'
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Helmet } from 'react-helmet';

export default function PasswordLoggedChange() {
  let headers = {token:localStorage.getItem('userToken')} 
let navigate = useNavigate()

 const [loading, setloading] = useState(false)
 const [mesggeerror, setmesggeerror] = useState('')

async function changeLoggedPassword(values)
{
  setloading(true)

let {data}  = await axios.put(`https://route-ecommerce-app.vercel.app/api/v1/users/changeMyPassword` , values  , {headers:headers} ).catch((err)=>{
  setloading(false) 
  setmesggeerror(`${err.response.data.message}`)
})
  if(data.message === 'success')
  {
    setloading(false)
    toast.success('your password have ben changed', { duration: 2000 })
    navigate('/login')
   }
  console.log(data);
}


let validation = Yup.object({
  currentPassword:Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/,'password must start with upper case '),
  password:Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/,'password must start with upper case '),
  rePassword:Yup.string().required('password is required').oneOf([Yup.ref('password')],'password must start with upper case '),

})



let formik = useFormik({
  initialValues:{
    currentPassword:"",
    password:"",
    rePassword:""
 
    
  },


  validationSchema:validation,
  onSubmit:changeLoggedPassword
});

  return <>
       <Helmet>
                <title>Change Password</title>
            </Helmet>

   <div className="container">
   <div className="w-75 py-4 mx-auto">
   <form onSubmit={formik.handleSubmit}>

   <h2>Login Now :</h2>

   {mesggeerror ? <div className="alert alert-danger">{mesggeerror}</div> : null } 

   <label htmlFor="currentPassword">currentPassword:</label>
   <input onBlur={formik.handleBlur} onChange={formik.handleChange}  value={formik.values.currentPassword} className='form-control my-2 w-100' type="Password" name='currentPassword' id='currentPassword' />
   {formik.errors.currentPassword && formik.touched.currentPassword ? <div className="alert alert-success">{formik.errors.currentPassword}</div>: null } 

   <label htmlFor="password">Password:</label>
   <input onBlur={formik.handleBlur} onChange={formik.handleChange}  value={formik.values.password} className='form-control my-2 w-100' type="password" name='password' id='password' />
   {formik.errors.password && formik.touched.password ? <div className="alert alert-success">{formik.errors.password}</div>: null } 

   <label htmlFor="rePassword">rePassword:</label>
   <input onBlur={formik.handleBlur} onChange={formik.handleChange}  value={formik.values.rePassword} className='form-control my-2 w-100' type="password" name='rePassword' id='rePassword' />
   {formik.errors.rePassword && formik.touched.rePassword ? <div className="alert alert-success">{formik.errors.rePassword}</div>: null } 






   {loading ? <button  className='btn bg-main text-white' type='submit'><i className='fas fa-spinner fa-spin'></i></button>

   :<button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white' type='submit'>Regist Now</button>}
  
 </form>
 {/* <Link to={'/forgetPassword'} className='text-main'>Forget Password </Link> */}
 </div>

   </div>
  </>

}












































