import './App.css';
import Layout from './components/Layout/Layout';
import { Navigate, RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import Cart from './components/Cart/Cart';
import About from './components/About/About';
import Cateogry from './components/Cateogry/Cateogry';
import Products from './components/Products/Products';
import Login from './components/Login/Login';
import Register from './components/Register/Register';
import Home from './components/Home/Home';
import Brands from './components/Brands/Brands';
import NotFound from './components/NotFound/NotFound';
import { useEffect, useState } from 'react';
import jwtDecode from 'jwt-decode';
import { CartContextProvider } from './context/CartContext';
import toast, { Toaster } from 'react-hot-toast';
import BrandDetails from './components/BrandDetails/BrandDetails';
import CategoryDetails from './components/CategoryDetails/CategoryDetails';
import ProductDetiles from './components/ProductDetiles/ProductDetiles';
import ResetPassword from './components/ResetPassword/ResetPassword';
import ChangePassword from './components/ChangePassword/ChangePassword';
import PasswordLoggedChange from './components/PasswordLoggedChange/PasswordLoggedChange';
import CheckOut from './components/CheckOut/CheckOut';
import ForgetPassword from './components/ForgetPassword/ForgetPassword';
import { Offline, Online } from "react-detect-offline";

function App() {

  useEffect(() => {
    if(localStorage.getItem('userToken') !== null )
    {
      saveUserData()

    }
  }, [])

const [userData, setuserData] = useState(null)

  function saveUserData()
  {
  let encodedToken = localStorage.getItem('userToken')
  let decodedToken = jwtDecode(encodedToken)
  setuserData(decodedToken)

  }

function ProtectedRoute(props)
{
  if(localStorage.getItem('userToken') == null)
  {
    return <Navigate to={'/login'}/>

  }
  else
  {
    return props.children
  }

}

  let routers = createHashRouter([
    {path:'' , element:<Layout setuserData={setuserData} userData={userData}/> , children:[
      {index:true , element:<ProtectedRoute><Home/></ProtectedRoute>},
      {path:'cart' ,element:<ProtectedRoute><Cart/></ProtectedRoute> },
      {path:'about' ,element:<ProtectedRoute><About/></ProtectedRoute>},
      {path:'cateogry' ,element:<ProtectedRoute><Cateogry/></ProtectedRoute> },
      {path:'products' ,element:<ProtectedRoute><Products/></ProtectedRoute> },
      {path:'brands' ,element:<ProtectedRoute><Brands/></ProtectedRoute> },
      {path:'productDetiles/:id' ,element:<ProtectedRoute><ProductDetiles/></ProtectedRoute> },
      {path:'categoryDetails/:id' ,element:<ProtectedRoute><CategoryDetails/></ProtectedRoute> },
      {path:'brandDetails/:id' ,element:<ProtectedRoute><BrandDetails/></ProtectedRoute> },
      {path:'checkout',element:<ProtectedRoute><CheckOut/></ProtectedRoute> },
      {path:'passwordLoggedChange',element:<ProtectedRoute><PasswordLoggedChange/></ProtectedRoute> },
      {path:'changePassword',element:<ChangePassword/>},
      {path:'forgetPassword',element:<ForgetPassword/>},
      {path:'resetPassword',element:<ResetPassword/>},
      {path:'login' ,element:<Login saveUserData={saveUserData}/> },
      {path:'register' ,element:<Register/> },
    
      {path:'*' ,element:<NotFound/> },
  

    ]}
  ])



  return <> 

<CartContextProvider>
{/* <Online>Only shown when you're online</Online> */}



    <Toaster/> 
  <RouterProvider router={routers}></RouterProvider>
 
<Offline><div className="offline-mode">you are currently offline <i class="fa-regular fa-face-frown"></i></div></Offline>


  </CartContextProvider>

  
  </>
}

export default App;
